@import "./src/common/styles/variables";

* {
  box-sizing: border-box;
}

.bodyBlock section:nth-child(2n) {
  border: 1px solid $bgSecondColor;
  background-color: $bgSecondColor;
}

.bodyBlock section:nth-child(2n+1) {
  border: 1px solid $bgFirstColor;
  background-color: $bgFirstColor;
}
